body {
  background-color: black;
  text-align: center;
}

.bars{ 
  display: 'flex';
  flex-direction: 'column';
  align-items: 'center';
  margin-left: 10%;
  margin-right: 10%;
}

.progress-header{
  color: lime;
  font-family: "Lucida Console", "Courier New", monospace;
}

.progress-text{
  margin-top: 25px;
    color: lime;
    font-family: "Lucida Console", "Courier New", monospace;
}

.progress-bar {
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
  background-color: darkgrey;
  position: relative;
  border-radius: 5px;
  margin-bottom: 5px;
}

.progress {
  height: 100%;
  background-color: lime;
  border-radius: 5px;
}